export default {
	data() {
		return {
			searchContent: []
		}
	},
	methods: {
		input(e) {
			const {
				value
			} = e
			clearTimeout(timer)
			let timer = setTimeout(() => {
				this.getData(value)
			}, 1000)

		},
		async getData(value) {
			// if(value === ""){
			// 	return
			// }
			const res = await this.$request({
				url: "/v1/goods/qsearch",
				data: {
					query: value.trim()
				}
			})
			// console.log(res)
			this.searchContent = res
		},
		handleTo(id){
			uni.navigateTo({
				url:`../goodsInfo/goodsInfo?goods_id=${id}`
			})
		}
	}
}
